<template>
  <div class="login">
    <div class="login-box">      
      <van-nav-bar>
        <template #left>
          <div class="logo-box">
            <div class="logo">
              <img class="auto-img" src="../assets/images/logo.png" alt="">
            </div>
            <div class="logo-title">Luckin Coffee</div>
          </div>
        </template>
        <template #right>
          <div class="right-title" @click="goHome">跳过</div>
        </template>
      </van-nav-bar>


      <div class="login-form-box">
        <div class="title">欢迎回来！</div>
        <div class="sub-title">Please login to your accounts</div>
        <div class="login-form">
          <van-form>
            <van-field
              v-model="userLoginInfo.phone"
              label="手机号"
              placeholder="手机号"
            />
            <van-field
              v-model="userLoginInfo.password"
              :type="isLoginPassword ? 'password' : 'text'"
              label="密码"
              placeholder="密码"
              :right-icon="isLoginPassword ? 'closed-eye' : 'eye-o'"
              @click-right-icon="viewLoginPassword"
            />
            <div class="forget-box clearfix">
              <span class="fr" @click="toForgot">忘记密码？</span>
            </div>
            <div class="btns-box">
              <van-button class="login-btn btn" block type="info" native-type="submit" @click='login'>
                登&nbsp;&nbsp;录
              </van-button>
              <van-button class="register-btn btn" block type="default" @click="showPopup">
                注&nbsp;&nbsp;册
              </van-button>
            </div>
          </van-form>

        </div>
      </div>

      <div class="popup-box">
        <van-popup v-model="isRegister" overlay-class='overlay' closeable round position="bottom" :style="{ height: '60%' }">
          <div class="register-box">
            <van-form>
              <div class="title">注册</div>
              <van-field
                v-model="userRegisterInfo.nickName"
                type="text"
                label="昵称"
                placeholder="至少一位字符"
              />
              <van-field
                v-model="userRegisterInfo.phone"
                label="手机号"
                placeholder="手机号 中国(+86)"
              />
              <van-field
                v-model="userRegisterInfo.password"
                :type="isRegisterPassword ? 'password' : 'text'"
                label="密码"
                placeholder="6-12位由数字和字母组成的字符"
                :right-icon="isRegisterPassword ? 'closed-eye' : 'eye-o'"
                @click-right-icon="viewRegisterPassword"
              />
              <div class="btns-box">
                <van-button class="register-btn btn" block native-type="submit" @click="register">
                  注&nbsp;&nbsp;册
                </van-button>
              </div>

            </van-form>
          </div>

        </van-popup>
      </div>

    </div>
  </div>
</template>

<script>
  import '../assets/less/login.less'
  import {validForm} from '../assets/js/validForm'
  import {utils} from '../assets/js/utils'

  export default {
    data(){
      return{
        isLoginPassword:true,
        isRegisterPassword:true,
        userLoginInfo:{
          phone:'',
          password:'',
        },
        userRegisterInfo:{
          nickName:'',
          phone:'',
          password:'',
        },
        // 是否显示注册面板
        isRegister:false,
      }
    },
    methods:{
      // 回到首页
      goHome(){
        this.$router.push({name:'Home'});
      },
      // 显示登录密码
      viewLoginPassword(){
        this.isLoginPassword = !this.isLoginPassword;
      },
      // 显示注册页的密码
      viewRegisterPassword(){
        this.isRegisterPassword = !this.isRegisterPassword;
      },
      // 显示弹出层 注册面板
      showPopup(){
        this.isRegister = true;
      },
      // 注册
      register(){
        let userInfo = this.userRegisterInfo;
        // // console.log(userInfo === this.userRegisterInfo)

        // 验证注册表单
        let o = {
          nickName: {
            value: userInfo.nickName,
            reg: /^[\w\d\u4e00-\u9fa5]{1,10}$/,
            errorMsg: '昵称格式不正确'
          },
          phone: {
            value: userInfo.phone,
            reg: /^1[3-9]\d{9}$/,
            errorMsg: '手机格式不正确'
          },
          password: {
            value: userInfo.password,
            // 6-12位由数字和字母组成的字符
            reg: /^(?![0-9]+$)(?![a-zA-Z]+$)[a-zA-z0-9]{6,12}$/,
            errorMsg: '密码格式不正确'
          }
        }

        if(!validForm.valid(o)){
          // 验证不通过
          // console.log("验证不通过")
          return;
        }

        // Toast.loading({
          //   message: '加载中...',
        //   forbidClick: true,
        //   duration: 0
        // })

        // 发起请求
        // 开启加载提示
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        // 系列化参数
        let info = Object.assign({},userInfo);
        info.appkey = this.appkey;
        let data = utils.queryString(info);
        // console.log(data);

        // 返回promise
        this.axios({
          method: 'POST',
          url: '/register',
          data
        }).then( result => {
          // 关闭加载提示
          this.$toast.clear();

          // 如果注册成功
          if(result.data.code == 100){
            // 提示注册成功
            this.$toast.success(result.data.msg);
            // 关闭弹出层
            this.isRegister = false;
            // 清空数据表单的数据
            for(let key in userInfo){
              userInfo[key] = '';
            }
          }else{
            // 注册失败，提示信息
            this.$toast(result.data.msg)
          }
          // console.log('result ==> ',result);
        }).catch( err => {
          this.$toast.clear();
          // console.log('err ==> ',err);
        })


      },

      // 登录
      login(){
        let userInfo = this.userLoginInfo;

        let o = {
          phone: {
            value: userInfo.phone,
            reg: /^1[3-9]\d{9}$/,
            errorMsg: '手机格式不正确'
          },
          password: {
            value: userInfo.password,
            // 6-12位由数字和字母组成的字符
            reg: /^(?![0-9]+$)(?![a-zA-Z]+$)[a-zA-z0-9]{6,12}$/,
            errorMsg: '密码格式不正确'
          }
        }

        // 验证不通过
        if(!validForm.valid(o)){
          return;
        }

        // 加载提示
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        let info = Object.assign({},userInfo);
        info.appkey = this.appkey;
        let data = utils.queryString(info);

        this.axios({
          method: 'POST',
          url: '/login',
          data
        }).then( result => {
          // 登录成功
          if(result.data.code == 200){
            this.$toast.clear();

            for(let key in userInfo){
              userInfo[key] = '';
            }
            // 存储token
            localStorage.setItem('CSTK',result.data.token);
            // 跳转到首页
            this.$router.push({name:'Home'});
          }else{
            
            this.$toast(result.data.msg);
          }
          // console.log('reslut ==> ',result);
        }).catch( err => {
          this.$toast.clear();
          // console.log('err ==> ',err);
        })

      },
      // 跳转重置密码页
      toForgot(){
        this.$router.push({name: 'Forgot'})
      }

    }
  }
</script>

